$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.banner {
  .ant-carousel {
    width:360px;
    height:246px;
    margin: 30px 0 30px 14px;
    line-height: 160px;
    text-align: center;
    background: #364d79;
    .slick-dots {
      position: absolute;
      top: -15px;
      right: 0;
      width: auto;
      height:8px;
      li {
        width:8px;
        height:8px;
        background:rgba(255,210,210,1);
        border-radius:50%;

        button {
          width:8px;
          height:8px;
          background:#ef0716;
          border-radius:50%;
          opacity: 0;
        }
      }
      .slick-active {
        width:8px;
        height:8px;
        background:#ef0716;
        border-radius:50%;
      }
    }
  }
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}
.ant-carousel .slick-slide img{
  width:360px;
  height:246px;
}
