$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.tool_wrap {
  position: fixed;
  top: 50%;
  right: 0;
  display: -webkit-flex;
  display: flex;
  width: 56px;
  text-align: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #fff;
  border-radius:6px;
  box-shadow:0 4px 8px 0 rgba(64,64,64,.2);
  overflow: hidden;
  .ant-back-top {
    width: 100%;
    position: relative;
    color: #909090;
    right: 0;
    bottom: 0;
  }
  .tool_item {
    width: 100%;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    font-size: 13px;
    color: #909090;
    -webkit-flex-direction: column;
            flex-direction: column;

    -webkit-justify-content: center;

            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .tool_item:hover {
    color: #fff;
    background-color: $red;
  }
}
.ant-popover-content {
  text-align: center;
  .code {
    width: 142px;
    height: 142px;
  }
  .ant-popover-title {
    padding: 0;
    color: #fff;
    p {
      padding-top: 10px;
    }
    .wechat {
      overflow: hidden;
      img {
        margin: 10px auto;
      }
      background-color: #30C838;
    }
    .app {
      overflow: hidden;
      img {
        margin: 10px auto;
      }
      background-color: #392D49;
    }
  }
}
