$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}



.video-page {
  width: 100%;
  height: 100%;
  #video {
    width: 1200px;
    height: 600px;
    position: relative;

    .poster {
      width:142px;
      height:168px;
      background:rgba(179,71,255,1);
      border-radius:6px;
      position: absolute;
      left: 34px;
      bottom: 86px;
      z-index: 15;
      text-align: center;
      .close {
        position: absolute;
        top: -6px;
        right: -6px;
        background-color: #fff;
        border-radius: 50%;
      }
      p {
        font-size: 12px;
        color: #FFF600;
      }
      img {
        margin: 12px auto;
        width:96px;
        height:96px;
        background:rgba(255,255,255,1);
      }
    }
    .none {
      display: none;
    }
  }
}

chimee-popup {
	position: absolute;
	color: #fff;
	display: block;
  width:184px;
  height:49px;
  background:rgba(0,0,0, .4);
  border-radius:25px;
	width: 200px;
	left: 50%;
	top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  line-height: 40px;
  font-size: 20px;
}
chimee-popup:hover {
  background-color: $red;
}
chimee-volume.horizonal chimee-volume-bar-all {
  background-color: $red;
}

error-popup {
  position: absolute;
  left: 41%;
	top: 181px;
  font-weight: 500;
  font-size: 37px;
  pp-error-tips {
    display: block;
    color: #fff;
    font-size: 18px;
  }
  .red {
    color: $red;
  }
  .refurbish {
    cursor: pointer;
  }
}
