$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.home_wrap {
  width: 100%;
  .header_banner {
    width: 100%;
    height: 712px;
    overflow: hidden;
    background:-webkit-linear-gradient(top,rgba(120,97,254,1),rgba(179,71,255,1));
    background:linear-gradient(180deg,rgba(120,97,254,1),rgba(179,71,255,1));
    .main_banner {
      width: 1400px;
      height: 672px;
      margin: 20px auto;
      background:rgba(0,0,0, .4);

      .main_left {
        float: left;
        width: 1195px;
        height: 100%;
        position: relative;
        #video {
          width: 100%;
          height: 100%;
        }

        .info_wrap {
          width: 260px;
          height: 200px;
          position: absolute;
          color: #fff;
          top: 34px;
          left: 34px;
          z-index: 10;

          .avatar_info {
            width: 100%;
            height: 60px;
            display: -webkit-flex;
            display: flex;
            -webkit-align-items: center;
                    align-items: center;
            -webkit-justify-content: center;
                    justify-content: center;
            border-bottom: 1px solid #fff;
            padding-bottom: 10px;
            .user {
              width: 110px;
              margin: 0 10px;
              font-size: 15px;
              .nickname {
                overflow: hidden;
                white-space: nowrap;
                text-overflow:ellipsis;
              }
            }
            .focus_card {
              width:62px;
              height:30px;
              background:-webkit-linear-gradient(129deg,rgba(239,7,22,1),rgba(185,61,255,1),rgba(117,185,255,1));
              background:linear-gradient(-39deg,rgba(239,7,22,1),rgba(185,61,255,1),rgba(117,185,255,1));
              border-radius:15px;
              text-align: center;
              line-height: 30px;
            }
          }
          .room_info {
            width: 100%;
            display: -webkit-flex;
            display: flex;
            -webkit-justify-content: space-around;
                    justify-content: space-around;
            text-align: center;
            padding: 15px 0;
          }
        }
      }
      .main_right {
        float: left;
        width: 196px;
        height: 672px;
        ul {
          padding-left: 0;
          font-size: 0;
          li {
            position: relative;
            display: inline-block;
            width: 189px;
            height: 158px;
            margin: 4px 8px;
            font-size: 12px;
            list-style: none;
            background:rgba(226,226,226,1);
            border-radius: 6px;
            box-shadow:0 2px 4px 0 rgba(11,0,27,.4);
            img {
              width: 100%;
              height: 100%;
            }
          }
          li:first-of-type {
            margin-top: 8px;
          }
          .active {
            border: 2px solid $red;
            border-radius: 6px;
            width: 100%;
            height: 100%;
          }
          .active::after {
            position: absolute;
            top: 50%;
            left: -16px;
            z-index:1;
            display: block;
            width:0;
            height: 0;
            content: "";
            border: 7px solid transparent;
            border-right-color: $red;
          }
        }
      }
    }
  }

  .index_content {
    width: 100%;
    height: 100%;

    > .recommend {
      display: -webkit-flex;
      display: flex;
      width: 1400px;
      height: auto;
      margin: 55px auto;

      .index_left {
        margin-top: -13px;
      }
    }
  }
  .test_card {
    width: 100%;
    height: 300px;
    background: rebeccapurple;
    top: 800px;
    position: absolute;
  }
}
